import React from "react"

import Container from '../components/container'
import Head from '../components/head'
import Img from 'gatsby-image'
import { graphql } from 'gatsby'
import Fade from 'react-reveal/Fade'
import Testimonials from '../components/testimonials'

import '../styles/index.css'

class IndexPage extends React.Component {

    render() {
        const { data } = this.props

        return (
            <div>
                <Container>
                    <Head title="Home"/>
    
                    <Fade>
                        <div className="rowHero">
                            <div className="ctaCont">
    
                                <h2>
                                    Work is love,<br />
                                    made visible.
                                </h2>
    
                                <h4>
                                    Android Developer at <a href="https://in.bookmyshow.com/" target="_blank" rel="noopener noreferrer" id="bookmyshow">BookMyShow</a>.
                                    A Google-certified <a href="https://credential.net/xgjuoa5t" target="_blank" rel="noopener noreferrer" id="aad">Associate Android Developer </a>and 
                                    <a href="https://confirm.udacity.com/GJPAAZSG" target="_blank" rel="noopener noreferrer" id="adn"> Android Developer Nanodegree</a> recipient.
                                </h4>
    
                                <a href="mailto:akshanshjn9@gmail.com" rel="noopener noreferrer">
                                    <button className="button">GET IN TOUCH</button>
                                </a>
    
                            </div>
    
                            <div className="rowImgCont">
                                {!!data.landing && !!data.landing.childImageSharp
                                    ? <Img fluid={data.landing.childImageSharp.fluid} alt={"BMS Mockup"} />
                                    : <img src={data.landing.publicURL} alt={"BMS Mockup"} />
                                }
                            </div>
                        </div>
                    </Fade>
    
                    <Fade>
                        <div className="rowProcess">
                            <h2>
                                Take your business<br />
                                where the audience is
                            </h2>
                            <div className="processCont">
                                <div className="processItem">
                                    <h4>Design</h4>
                                    <p>
                                        I focus on clean and minimalistic visual experiences. Passionate about merging art of
                                        typography, colors and graphics with science of development and testing.
                                    </p>
                                </div>
                                <div className="processItem">
                                    <h4>Develop</h4>
                                    <p>
                                        Customized with the best in class architectural standards and complete understanding of the
                                        comprehensive development environment.
                                    </p>
                                </div>
                                <div className="processItem">
                                    <h4>Deploy</h4>
                                    <p>
                                        Great emphasis on testing, including functional usability and customer satisfaction, and
                                        reviews to ensure that the product is launch ready.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </Fade>

                    <Fade>
                        <Testimonials />
                    </Fade>
    
                </Container>
                
            </div>
        )
    }
}

export default IndexPage


export const projectImages = graphql`
    query ProjectImages {
        landing: file (relativePath: {eq: "bms.png" }) {
            childImageSharp {
                fluid(maxWidth: 2048) {
                    ...GatsbyImageSharpFluid
                }
            }
        }
    }
`